<template>
  <div>
    <div class="m-10 md:mt-40 relative">
      <model-viewer
        class="w-full absolute h-3/4"
        :src="`${URL}/artkonnection/100`"
        auto-rotate
      ></model-viewer>
      <div class="relative">
        <h1
          class="mt-10 font-black text-transparent md:text-7xl text-4xl bg-clip-text bg-gradient-to-br from-pink-500 to-purple-500 m-5 mb-10"
        >
          Welcome To Art Konnection
        </h1>
        <br />
        <h1
          class="font-black text-transparent md:text-5xl text-3xl bg-clip-text bg-gradient-to-br from-pink-500 to-purple-500 m-5 mt-40 md:mt-80 md:p-10"
        >
          Amazing Projects are waiting for you!
        </h1>
        <br /><br /><br />
      </div>
    </div>

    <div class="relative mx-1 mb-5">
      <span
        class="md:text-3xl font-semibold text-xl z-20 relative dark:text-white"
        >Art Konnection Base Token</span
      ><br />
      <div
        class="absolute -inset-0.5 blur bg-gradient-to-r from-pink-600 to-purple-500"
      ></div>
      <div class="flex justify-around relative bg-white dark:bg-black">
        <model-viewer
          class="m-3"
          :src="`${URL}/artkonnection/1`"
          auto-rotate
        ></model-viewer>
        <model-viewer
          class="m-3"
          :src="`${URL}/artkonnection/2`"
          auto-rotate
        ></model-viewer>
        <model-viewer
          class="m-3"
          :src="`${URL}/artkonnection/3`"
          auto-rotate
        ></model-viewer>
      </div>
    </div>

    <h1
      class="font-black text-transparent text-3xl bg-clip-text bg-gradient-to-br from-pink-500 to-purple-500 m-5 p-10"
    >
      Art Projects
    </h1>
    <div
      v-for="(item, i) in datas"
      :key="i"
      class="md:m-10 m-1 h-auto relative"
    >
      <div
        class="rounded-md absolute -inset-0.5 blur bg-gradient-to-r from-pink-600 to-purple-500 animate-pulse"
      ></div>
      <div class="relative rounded-md bg-white dark:bg-black">
        <div class="grid grid-cols-2 pt-5 lg:pt-10">
          <span
            class="text-xl md:text-2xl lg:text-4xl dark:text-white justify-self-start ml-5 font-semibold"
            >{{ item.projectName }} by {{ item.artist }}</span
          >

          <router-link
            :to="'/aboutcollection/' + item.id"
            class="justify-self-end mr-5 relative"
            ><div
              class="rounded-md absolute -inset-0.5 blur bg-gradient-to-r from-pink-600 to-purple-500 animate-pulse"
            ></div>
            <div
              class="dark:text-white h-full flex items-center relative rounded-md bg-white dark:bg-black font-semibold p-2"
            >
              About Collection
            </div>
          </router-link>
        </div>

        <Carousel
          :itemsToShow="3"
          :wrapAround="true"
          :autoplay="3000"
          class="flex items-center"
        >
          <Slide v-for="slide in 9" :key="slide">
            <div class="m-3">
              <img
                v-if="!item.notImage"
                :src="`${URL}/assets/${item.id * 1000000 + slide}`"
                alt="Token image"
                @error="item.notImage = true"
              />
              <model-viewer
                v-if="item.notImage"
                :src="`${URL}/assets/${item.id * 1000000 + slide}`"
                auto-rotate
              ></model-viewer>
            </div>
          </Slide>
        </Carousel>
      </div>
    </div>
  </div>
</template>

<script>
import { Carousel, Slide } from "vue3-carousel";
import { reactive, onMounted, ref } from "vue";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";

import "vue3-carousel/dist/carousel.css";

export default {
  components: {
    Carousel,
    Slide,
  },
  setup() {
    let datas = reactive([]);
    const URL = ref(process.env.VUE_APP_SERVER_URL);

    let projectNames = reactive([]);
    let nums = ref(0);
    let caver = null;
    let contract = null;
    onMounted(async () => {
      caver = new Caver(
        window.klaytn ? window.klaytn : process.env.VUE_APP_RPC_URL
      );
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      _nextProjectId();
    });
    const _projectDetails = async (_projectId) => {
      await contract.methods
        .projectDetails(_projectId)
        .call()
        .then((res) => {
          res.id = _projectId;
          res.notImage = false;
          if (!projectNames.includes(res.projectName) && nums.value < 4) {
            datas.push(res);
            nums.value++;
            projectNames.push(res.projectName);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const _nextProjectId = async () => {
      await contract.methods
        .nextProjectId()
        .call()
        .then((result) => {
          for (let i = 0; i < 10; i++) {
            const _projectId = Math.floor(Math.random() * result);
            _projectDetails(_projectId);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };

    return {
      datas,
      Carousel,
      URL,
      Slide,
    };
  },
};
</script>

<style scoped>
@media (min-width: 640px) {
  model-viewer {
    height: 400px;
  }
}
@media (min-width: 768px) {
  model-viewer {
    height: 300px;
  }
}
@media (min-width: 1024px) {
  model-viewer {
    height: 400px;
  }
}
@media (min-width: 1280px) {
  model-viewer {
    height: 600px;
  }
}

@media (min-width: 640px) {
  img {
    height: 200px;
  }
}
@media (min-width: 768px) {
  img {
    height: 300px;
  }
}
@media (min-width: 1024px) {
  img {
    height: 400px;
  }
}
@media (min-width: 1280px) {
  img {
    height: 600px;
  }
}
</style>
